(function() {
  'use strict';

  angular
    .module('mad')
    .directive('acmeNavbarAd', acmeNavbarAd);

  /** @ngInject */
  function acmeNavbarAd() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar-advert/navbar-advert.html',
      scope: {
        creationDate: '='
      },
      controller: NavbarAdController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarAdController($scope, $location) {
      $scope.isManage = false;
      $scope.isPush = false;
      $scope.isList = false;

      $scope.isPay = false;
      $scope.isPut = false;
      $scope.isBack = false;

      $scope.isStatistics = false;
      $scope.isAccount = false;
      $scope.isAdSta = false;

      var path = $location.path();
      console.log('path:' + path);
      if (path == '/advert/push') {
        $scope.isManage = true;
        $scope.isPush = true;
      }

      if (path == '/advert') {
        $scope.isManage = true;
        $scope.isList = true;
      }

      if (path == '/account/reacharge') {
        $scope.isPay = true;
        $scope.isPut = true;
      }

      if (path == '/account/refund') {
        $scope.isPay = true;
        $scope.isBack = true;
      }

      if (path == '/account/check') {
        $scope.isStatistics = true;
        $scope.isAccount = true;
      }
      if (path == '/statistics') {
        $scope.isStatistics = true;
        $scope.isAdSta = true;
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('mad')
    .controller('HeaderCtrl', HeaderCtrl);

  /** @ngInject */
  function HeaderCtrl($rootScope, $scope, $location, $state, AdvertiserSrv, TokenSrv, NoticeSrv) {
    $scope.isMain = false;

    $scope.isManage = false;
    $scope.isPush = false;
    $scope.isList = false;

    $scope.isPay = false;
    $scope.isPut = false;
    $scope.isBack = false;
    $scope.isConfirm = false;

    $scope.isStatistics = false;
    $scope.isAccSta = false;
    $scope.isAdSta = false;

    $scope.isMsg = false;

    $scope.isAccount = false;

    if ($location.path() == '/advert/push') {
      $scope.isManage = true;
      $scope.isPush = true;
    }

    if ($location.path() == '/advert') {
      $scope.isManage = true;
      $scope.isList = true;
    }

    if ($location.path() == '/account/recharge') {
      $scope.isPay = true;
      $scope.isPut = true;
    }

    if ($location.path() == '/account/refund') {
      $scope.isPay = true;
      $scope.isBack = true;
    }

    if ($location.path() == '/account/check') {
      $scope.isPay = true;
      $scope.isConfirm = true;
    }

    if ($location.path() == '/statistics') {
      $scope.isStatistics = true;
      $scope.isAccSta = true;
    }

    if ($location.path() == '/statistics/detail') {
      $scope.isStatistics = true;
      $scope.isAdSta = true;
    }

    if ($location.path() == '/account') {
      $scope.isAccount = true;
    }

    if ($location.path() == '/notification') {
      $scope.isMsg = true;
    }
    
    $('#signup').on('hidden.bs.modal', function (e) {
      $scope.thereIsError = false;
      $scope.errMessage = '';
      console.log('hide signup');
      
    });
    
    $('#login').on('hidden.bs.modal', function (e) {
      $scope.thereIsError = false;
      $scope.errMessage = '';
      console.log('hide login');
    })
    
    // console.log('token:' + TokenSrv.getToken());
    if (TokenSrv.getToken()) {
      $rootScope.logined = true;
      $scope.name = TokenSrv.getName();
    } else {
      $rootScope.logined = false;
    }
    $scope.isLogin = $rootScope.logined;
    $scope.thereIsError = false;
    
    // 注册
    $scope.register = function (nickname, email, pass, confirmPass) {
      if (!nickname || !email || !pass || !confirmPass) {
        $scope.thereIsError = true;
        $scope.errMessage = '请输入完整信息';
        return;
      }
      if (pass !== confirmPass) {
        $scope.thereIsError = true;
        $scope.errMessage = '两次输入密码不一致';
        return;
      }
      if (email.search(/^\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/) === -1) {
        $scope.thereIsError = true;
        $scope.errMessage = '请输入合法邮箱';
        return;
      }
      
      $scope.thereIsError = false;
      var sha256Pass = sha256(pass);
      
      AdvertiserSrv.register().save({
          username: nickname,
          email: email,
          password: sha256Pass
        }).$promise.then(
          function (response) {
            console.log(response);
            if (0 === response.errCode) {
              console.log('注册成功');
              NoticeSrv.success('恭喜您注册成功，请您验证邮箱后登录');
              $('#signup').modal('hide');
            } 
          }, function (error) {
            console.log('注册失败');
            console.log(error);
            $scope.thereIsError = true;
            $scope.errMessage = '未知错误' + error;
          });
      
    };
    
    // 登录
    $scope.login = function (email, pass) {
      if (email == null || pass == null) {
        $scope.thereIsError = true;
        $scope.errMessage = '请输入用户名和密码';
        return;
      }
      if (email.search(/^\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/) === -1) {
        $scope.thereIsError = true;
        $scope.errMessage = '请输入合法邮箱';
        return;
      }
      
      var sha256Pass = sha256(pass);
      // console.log('sha256:' + sha256Pass);
      
      AdvertiserSrv.login().save({
        email: email,
        password: sha256Pass
      }).$promise.then(
        function (response) {
          console.log(response);
          if (0 === response.errCode) {
            console.log('登录成功');
            TokenSrv.setToken(response.token);
            TokenSrv.setName(response.name);
            $('#login').modal('hide');
            $scope.isLogin = true;
            $scope.name = response.name;
            $rootScope.logined = true;
          }
        }, function(error) {
          console.log('登录失败');
          console.log(error);
        });
    };
    
    $scope.logout = function () {      
      TokenSrv.setToken('');
      $scope.isLogin = false;
      $rootScope.logined = false;
      $state.go('app', {reload: true});
    };
  }
})();

