(function() {
  'use strict';

  angular
    .module('mad')
    .factory('TokenSrv', TokenSrv);

  /** @ngInject */
  function TokenSrv($cookies) {
    return {
          token: '',// for test:6e777a9556c6774e6b62ff9a18ec456e3f05181260cf1a129811a4a08152fbaa
          name: '',
          setToken: function (token) {
              this.token = token;
              $cookies.put('token', token);
          },
          getToken: function () {
            if (!this.token) {
              this.token = $cookies.get('token');
            }
            return this.token;
          },
          setName: function(name) {
            this.name = name;
            $cookies.put('name', name);
          },
          getName: function() {
            if (!this.name) {
              this.name = $cookies.get('name');
            }
            return this.name;
          }
        };
  }
})();

(function() {
  'use strict';

  angular
    .module('mad')
    .factory('NoticeSrv', NoticeSrv);

  /** @ngInject */
  function NoticeSrv($compile, $timeout, $rootScope) {
    var notification = {};

  notification.timer = null;
  notification.__alert__ = function(css, title, text) {
    var template = ['<div class="notice fixed alert alert-dismissible ',
                      css,
                      '">',
                      '<button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>',
                      '<strong>',
                      title,
                      '</strong>',
                      text,
                      '</div>'].join('');
    if (angular.element(document).find('.notice')) {
      angular.element(document).find('.notice').remove();
    }
    if (this.timer) {
      $timeout.cancel(this.timer);
    }
    angular.element(document).find('body').append($compile(template)($rootScope));
    this.timer = $timeout(function() {
      angular.element(document).find('.notice').remove();
    }, 5000);
  };

  notification.success = function(text) {
    this.__alert__('alert-success', '成功: ', text);
  };

  notification.error = function(text) {
    this.__alert__('alert-danger', '错误: ', text);

  };

  notification.notice = function(text) {
    this.__alert__('alert-info', '提示: ', text);
  };

  return notification;
    
    
  }
})();

(function() {
  'use strict';

  angular
    .module('mad')
    .factory('myInterceptor', myInterceptor);

  /** @ngInject */
  function myInterceptor($rootScope, $injector, TokenSrv, baseURL) {
    //   console.log('helloworld');
    var myInterceptor = {
                request: function (config) {
                    // 通过配置拦截器在发送请求前注入token
                    if (arguments[0].url.indexOf(baseURL) !== -1) {
                        if (arguments[0].method === 'GET' || arguments[0].method === 'DELETE' || arguments[0].method === 'OPTIONS') {
                            arguments[0].params = arguments[0].params || {};
                            // console.log(arguments);
                            arguments[0].params.token = TokenSrv.getToken();
                        }
                        if (arguments[0].method === 'POST' || arguments[0].method === 'PUT') {
                            arguments[0].data = arguments[0].data || {};
                            arguments[0].data.token = TokenSrv.getToken();
                        }
                    }

                    // 必须用过injector注入$state,  不然会有BUG,
                    // 详见http://stackoverflow.com/questions/20230691/injecting-state-ui-router-into-http-interceptor-causes-circular-dependency
                    var $state = $injector.get('$state');
                    // 不在登陆页面且未登陆
                    // console.log($state.current.url);
                    if ( !$rootScope.logined && ($state.current.url !== '\\' && $state.current.url !== '^')) {
                        $injector.get('$state').go('app');
                    }
                    if (!TokenSrv.getToken()) {
                        window.location.href = '#/';
                    }
                    return config;
                },
                response: function (response) {
                  // response.data.errCode === 101
                  // response.errCode = 101
                    if (response.data.errCode === 101) {
                        console.log('errCode:' + response.data.errCode);
                        $injector.get('NoticeSrv').notice($injector.get('ErrorSrv').getError(response.data.errCode + ""));
                        TokenSrv.setToken('');
                        
                        var $state = $injector.get('$state');
                        $injector.get('$state').go('app');
                    }
                    if (response.data.errCode && response.data.errCode !== 0) {
                        $injector.get('NoticeSrv').error($injector.get('ErrorSrv').getError(response.data.errCode + ""));
                        console.log('errCode:' + response.data.errCode);
                    }


                    return response;
                }
            };
            return myInterceptor;
  }
})();

    

